"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";

interface BusRequest {
  id: number;
  name: string;
  pickupLocation: string;
  destination: string;
  date: string;
  time: string;
  status: "Pending" | "Approved" | "Rejected";
}

interface BusSchedule {
  id: number;
  route: string;
  date: string;
  time: string;
}

export default function StaffDashboardPage() {
  const location = "Amarillo, Texas";

  const [requests, setRequests] = useState<BusRequest[]>([
    {
      id: 1,
      name: "John Doe",
      pickupLocation: "123 Main St",
      destination: "Community Center",
      date: "2024-07-01",
      time: "10:00",
      status: "Pending",
    },
  ]);
  const [schedules, setSchedules] = useState<BusSchedule[]>([]);
  const [route, setRoute] = useState("");
  const [date, setDate] = useState("");
  const [time, setTime] = useState("");
  const [message, setMessage] = useState("");
  const [showMessage, setShowMessage] = useState(false);

  useEffect(() => {
    if (message) {
      setShowMessage(true);
      const timer = setTimeout(() => setShowMessage(false), 3000);
      return () => clearTimeout(timer);
    }
  }, [message]);

  const handleApprove = (id: number) => {
    setRequests((prev) =>
      prev.map((req) =>
        req.id === id ? { ...req, status: "Approved" } : req
      )
    );
  };

  const handleReject = (id: number) => {
    setRequests((prev) =>
      prev.map((req) =>
        req.id === id ? { ...req, status: "Rejected" } : req
      )
    );
  };

  const handleAddSchedule = (e: React.FormEvent) => {
    e.preventDefault();
    if (!route || !date || !time) {
      setMessage("Please fill in all schedule fields.");
      return;
    }
    const newSchedule: BusSchedule = {
      id: Date.now(),
      route,
      date,
      time,
    };
    setSchedules([...schedules, newSchedule]);
    setRoute("");
    setDate("");
    setTime("");
    setMessage("Schedule added successfully.");
  };

  return (
    <>
      <style jsx global>{`
        @import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700;800&display=swap');
        body {
          font-family: 'Inter', sans-serif;
          background-color: #fff;
          color: #000;
          margin: 0;
          padding: 0;
        }
        .animate-fadeIn {
          animation: fadeIn 0.5s ease-in-out forwards;
        }
        @keyframes fadeIn {
          from {
            opacity: 0;
            transform: translateY(-5px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }
        table {
          border-collapse: collapse;
          width: 100%;
        }
        th, td {
          border: 1px solid #ccc;
          padding: 12px 15px;
          text-align: left;
        }
        thead {
          background-color: #f9f9f9;
        }
        tbody tr:nth-child(even) {
          background-color: #f2f2f2;
        }
        tbody tr:hover {
          background-color: #eaeaea;
        }
        button {
          cursor: pointer;
          transition: background-color 0.3s ease, color 0.3s ease;
        }
        button:hover {
          background-color: #000;
          color: #fff;
        }
        form {
          max-width: 400px;
          margin: 0 auto;
        }
        label {
          display: block;
          margin-bottom: 6px;
          font-weight: 600;
        }
        input {
          width: 100%;
          padding: 8px 10px;
          margin-bottom: 16px;
          border: 1px solid #ccc;
          border-radius: 4px;
          font-size: 1rem;
        }
        h1, h2, h3 {
          font-weight: 800;
          margin-bottom: 16px;
        }
        .container {
          max-width: 960px;
          margin: 40px auto;
          padding: 0 20px;
        }
      `}</style>
      <div className="container">
        <header className="text-center mb-12">
          <h1>Staff Dashboard - {location}</h1>
        </header>

        <section>
          <h2>Bus Requests</h2>
          {requests.length === 0 ? (
            <p>No bus requests available.</p>
          ) : (
            <table>
              <thead>
                <tr>
                  <th>Name</th>
                  <th>Pickup Location</th>
                  <th>Destination</th>
                  <th>Date</th>
                  <th>Time</th>
                  <th>Status</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {requests.map((req, idx) => (
                  <tr key={req.id}>
                    <td>{req.name}</td>
                    <td>{req.pickupLocation}</td>
                    <td>{req.destination}</td>
                    <td>{req.date}</td>
                    <td>{req.time}</td>
                    <td
                      style={{
                        color:
                          req.status === "Approved"
                            ? "green"
                            : req.status === "Rejected"
                            ? "red"
                            : "orange",
                        fontWeight: "600",
                      }}
                    >
                      {req.status}
                    </td>
                    <td>
                      {req.status === "Pending" && (
                        <>
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => handleApprove(req.id)}
                          >
                            Approve
                          </Button>
                          <Button
                            variant="destructive"
                            size="sm"
                            onClick={() => handleReject(req.id)}
                          >
                            Reject
                          </Button>
                        </>
                      )}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </section>

        <section style={{ marginTop: "40px" }}>
          <h2>Manage Bus Schedules</h2>
          <form onSubmit={handleAddSchedule}>
            <Label htmlFor="route">Route</Label>
            <Input
              id="route"
              type="text"
              value={route}
              onChange={(e) => setRoute(e.target.value)}
              required
            />
            <Label htmlFor="date">Date</Label>
            <Input
              id="date"
              type="date"
              value={date}
              onChange={(e) => setDate(e.target.value)}
              required
            />
            <Label htmlFor="time">Time</Label>
            <Input
              id="time"
              type="time"
              value={time}
              onChange={(e) => setTime(e.target.value)}
              required
            />
            {showMessage && (
              <p className="animate-fadeIn" style={{ color: "green", textAlign: "center" }}>
                {message}
              </p>
            )}
            <Button type="submit" style={{ width: "100%", marginTop: "10px" }}>
              Add Schedule
            </Button>
          </form>

          {schedules.length === 0 ? (
            <p style={{ marginTop: "20px", fontStyle: "italic", textAlign: "center" }}>
              No schedules added yet.
            </p>
          ) : (
            <div style={{ marginTop: "20px" }}>
              <h3>Current Schedules</h3>
              <ul>
                {schedules.map((schedule) => (
                  <li key={schedule.id}>
                    {schedule.route} - {schedule.date} at {schedule.time}
                  </li>
                ))}
              </ul>
            </div>
          )}
        </section>
      </div>
    </>
  );
}
